import moment from 'moment';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Table, TableBody, TableCell, TableContainer, TableRow, Box, Typography, TableHead } from '@mui/material';
import OnceIcon from '@mui/icons-material/PowerOutlined';
import ProxyIcon from '@mui/icons-material/ElectricalServicesOutlined';
import StartIcon from '@mui/icons-material/AccountTreeOutlined';
import ChildIcon from '@mui/icons-material/AccountTree';
import SleepIcon from '@mui/icons-material/TimerOutlined';
import SearchIcon from '@mui/icons-material/ManageSearchOutlined';
import { useTheme } from '@mui/material/styles';

import { DurableJobExport, TimelineElement, TimelineValue } from '../../types/meshdata';

interface WorkflowProps {
  workflow: DurableJobExport | undefined;
  workflowId: string | undefined;
}

const viewAnalytics = (jid: string) => {
  console.log(`Viewing analytics for ${jid}`);
};

const DependentsView: React.FC<WorkflowProps> = ({ workflow }) => {
  const theme = useTheme()
  const rows = workflow?.timeline || [];
  const navigate = useNavigate();
  const [duration, setDuration] = useState<string>('');
  const [created, setCreated] = useState<string>('');
  const [updated, setUpdated] = useState<string>('');
  const [active , setActive] = useState<boolean>(false);

  useEffect(() => {
    if (workflow?.state?.metadata?.jc && workflow?.state?.metadata?.ju) {
      const jobCreated = moment.utc(workflow.state.metadata.jc, 'YYYYMMDDHHmmss.SSS');
      setActive(workflow.status !== 0);
      const jobUpdated = active ? moment.utc() : moment.utc(workflow.state.metadata.ju, 'YYYYMMDDHHmmss.SSS');
      const durationMoment = moment.duration(jobUpdated.diff(jobCreated));
      const durationText = durationMoment.humanize();
      setDuration(durationText);
      setCreated(jobCreated.format('LLL'));
      setUpdated(jobUpdated.format('LLL'));
    }
  }, [workflow]);

  const viewWorkflow = (row: TimelineElement) => {
    if (!(typeof row.value === 'object' && ('job_id' in (row.value as object)))) return;
    const jid = (row.value as TimelineValue).job_id;
    const queryParams = new URLSearchParams(window.location.search);
    const app = workflow?.state?.metadata?.app;
    //todo meshdata.mintKey
    const jobId = `hmsh:${app}:j:${jid}`;
    navigate(`/workflows/${jobId}?${queryParams.toString()}`, { replace: false });
  };

  const viewConnections = (jid: string) => {
    if (!jid.startsWith('-')) {
      const [entity] = jid.split('-');
      if (entity.indexOf('.') == -1) {
        navigate(`/connections/topic:${entity}-${entity}`);
        return;
      }
    }
    navigate(`/connections`);
    console.log(`Viewing network connections for ${jid}`);
  };

  const viewEvent = (jid: string) => {
    console.log(`Viewing event for ${jid}`);
  };

  const resolveType = (row: any): string => {
    return row.key.split('-')[1];
  }

  const resolveLabel = (row: any) => {
    const type = resolveType(row);
    const [baseType, ...rest] = type.split(',');
    const dimString = rest.length ? `[${rest.join(', ')}]  ` : '[MAIN]  ';
    if (row.value) {
      if (row.value.job_id) {
        return `${dimString} JOB ID ${row.value.job_id}`;
      } else if (type.startsWith('search') && !isNaN(row.value)) {
        //format value as a locale string
        return `${dimString} SEARCH (set | delete | increment) = ${Number(row.value).toLocaleString('en-US')}`;
      } else if (type.startsWith('sleep')) {
        if (row.value.duration === 3155760000) {
          return `${dimString} SLEEP (infinity ttl)`;
        }
        return `${dimString} SLEEP (seconds) = ${Number(row.value.duration).toLocaleString('en-US')}`;
      } else if (type.startsWith('once')) {
        return `${dimString} ONCE (idempotent)`;
      }
    }
    return resolveType(row);
  };

  const resolveTimeSlot = (row: TimelineElement, index: number) => {
    if (row.value) {
      const val = row.value as TimelineValue;
      if (val.job_id || val.duration) {
        const jobCreated = moment.utc(workflow?.state?.metadata?.jc, 'YYYYMMDDHHmmss.SSS');
        const jobUpdated = active ? moment.utc() : moment.utc(workflow?.state?.metadata?.ju, 'YYYYMMDDHHmmss.SSS');
        const jobDuration = moment.duration(jobUpdated.diff(jobCreated));

        if (val.duration) {
          let percent = 0;
          if (val.ac) {
            const activityStart = moment.utc(val.ac, 'YYYYMMDDHHmmss.SSS');
            const offsetDuration = moment.duration(activityStart.diff(jobCreated));
            percent = (offsetDuration.asMilliseconds() / jobDuration.asMilliseconds()) * 100;
          } else if(val.au) {
            const activityStop = moment.utc(val.au, 'YYYYMMDDHHmmss.SSS');
            const offsetDuration = moment.duration(activityStop.diff(jobCreated));
            percent = (offsetDuration.asMilliseconds() / jobDuration.asMilliseconds()) * 100;
          }
          const isInfinity = val.duration === 3155760000;
          let width: string;
          let left: string;
          if (isInfinity) {
            left = '0';
            width = '100%';
          } else {
            left = `${percent}%`;
            width = `${((val.duration * 1000) / jobDuration.asMilliseconds()) * 100}%`;
          }
          return <div
            style={{
              position: 'absolute',
              top: 0,
              left: `${left}`,
              width: `${width}`,
              height: '100%',
              backgroundColor: theme.palette.action.disabled,
              borderRadius: '2px',
              borderColor: theme.palette.primary.light,
              borderLeft: 'solid 1px',
              borderRight: 'solid 1px',
              paddingRight: '3em',
              boxSizing: isInfinity ? 'content-box' : 'border-box',
              cursor: 'default',
              zIndex: 0,
              opacity: theme.palette.action.disabledOpacity,
            }}></div>;
        }
        const activityStart = moment.utc(val.ac, 'YYYYMMDDHHmmss.SSS');
        const offsetDuration = moment.duration(activityStart.diff(jobCreated));
        const percent = (offsetDuration.asMilliseconds() / jobDuration.asMilliseconds()) * 100;
        const width = '2em';
        const left = `${percent}%`;
        return <div
          onClick={() => viewWorkflow(row)}
          style={{
            position: 'absolute',
            top: 0,
            left: `${left}`,
            width: `${width}`,
            height: '100%',
            backgroundColor: theme.palette.primary.main,
            borderRadius: '2px',
            borderColor: theme.palette.primary.light,
            borderLeft: 'solid 1px',
            borderRight: 'solid 1px',
            cursor: 'pointer',
            zIndex: 0,
            opacity: 0.5,
          }}></div>;
      }
    }
    return null;
  }

  const resolveIcon = (row: any) => {
    const type = resolveType(row);
    if (type.startsWith('child')) {
      return <ChildIcon sx={{ fontSize: '1.25em', verticalAlign: 'middle', mr: '.25em' }} />;
    } else if (type.startsWith('start')) {
      return <StartIcon sx={{  fontSize: '1.25em', verticalAlign: 'middle', mr: '.25em' }} />;
    } else if (type.startsWith('once')) {
      return <OnceIcon sx={{ fontSize: '1.25em', verticalAlign: 'middle', mr: '.25em' }} />;
    } else if (type.startsWith('proxy')) {
      return <ProxyIcon sx={{ fontSize: '1.25em', verticalAlign: 'middle', mr: '.25em' }} />;
    } else if (type.startsWith('search')) {
      return <SearchIcon sx={{ fontSize: '1.5em', verticalAlign: 'middle', mr: '.25em' }} />;
    }
    return <SleepIcon sx={{ fontSize: '1.25em', verticalAlign: 'middle', mr: '.25em' }} />;
  };

  return (
    <Box sx={{ position: 'relative', height: '100%', display: 'flex', flexDirection: 'column' }}>
      <Box sx={{ display: 'flex', flexGrow: '1', flexDirection: 'column', height: '100%', overflow: 'hidden' }}>
        { rows.length ? <TableContainer component={Box} style={{ height: "100%", overflowX: 'hidden' }}>
          <Table size="small" stickyHeader aria-label="dependent data table">
            <TableHead>
              <TableRow>
                <TableCell component="th" style={{ fontWeight: 'bold' }}>Subordinated Activity</TableCell>
                <TableCell component="th">&nbsp;</TableCell>
              </TableRow>
            </TableHead>

            <TableBody>
              {rows.map((row, index) => (
                <TableRow key={row.key}>
                  <TableCell sx={{ position: 'relative' }}>
                    {resolveIcon(row)}
                    {resolveLabel(row)}
                    {resolveTimeSlot(row, index)}
                  </TableCell>
                  <TableCell sx={{width: '3em'}}>&nbsp;</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer> : <center style={{ marginTop: '6.63em', fontStyle: 'italic' }}>N / A</center> }
      </Box>
      <Box sx={{ display: 'flex', padding: '.5em .25em 0em .25em', justifyContent: 'space-between', width: '100%' }}>
        <Typography variant="body2"><strong>Start</strong> {created}</Typography>
        <Typography variant="body2"><strong>Duration</strong> {duration}</Typography>
        <Typography variant="body2"><strong>Stop</strong> {updated}</Typography>
      </Box>
    </Box>
  );
};

export default DependentsView;
