import React, { useEffect, useState } from 'react';
import { Box, Typography, IconButton, Link } from '@mui/material';
import TimelineIcon from '@mui/icons-material/Update';
import DataObjectIcon from '@mui/icons-material/TableRowsOutlined';
import StateIcon from '@mui/icons-material/DataObject';
import { useTheme } from '@mui/material/styles';

import StatusIcon from '@mui/icons-material/InfoOutlined';
import { DurableJobExport } from '../types/meshdata';

import DataView from './workflow/DataView';
import TimelineView from './workflow/TimelineView';
import StateView from './workflow/StateView';
import StatusView from './workflow/StatusView';
import { useNavigate } from 'react-router-dom';
import { truncateWithEllipsis } from '../modules/utils';

interface WorkflowProps {
  workflow: DurableJobExport | undefined;
  workflowId: string | undefined;
  onRefresh: () => void;
}

enum View {
  Data,
  Timeline,
  State,
  Dependents,
  Status,
}

const Workflow: React.FC<WorkflowProps> = ({ workflow, workflowId, onRefresh }) => {
  const [view, setView] = useState<View>(View.Data);
  const [shouldDisplay, setShouldDisplay] = useState(Boolean(workflowId));
  const [opacity, setOpacity] = useState(1);
  const [error, setError] = useState<string | null>(null); // New state for error handling
  const navigate = useNavigate();
  const theme = useTheme()

  useEffect(() => {
    if (workflowId) {
      if (workflow) {
        setOpacity(1);
        setShouldDisplay(true);
        setError(null); // No error if workflow is present
      } else {
        setError(`? ${workflowId} ?`); // Set error state if workflowId is present but workflow is null
        setOpacity(1);
        setShouldDisplay(true);
      }
    } else {
      setOpacity(0);
      setTimeout(() => {
        setShouldDisplay(false);
      }, 250); // Assuming the transition duration is 500ms
      setError(null); // Reset error state when workflowId is null
    }
  }, [workflowId, workflow]);

  const renderError = () => {
    // Inline style to update opacity to 1 after the component has rendered
    const errorStyle = {
      color: 'slategray',
      marginTop: '3em',
      padding: '1em',
      textAlign: 'center',
      opacity: 1,
      transition: 'opacity 2s ease-in',
    };
  
    return (
      <Typography
        variant="body1"
        sx={errorStyle}
        className="fadeIn"
      >
        {error}
      </Typography>
    );
  };
  

  const renderView = () => {
    if (error) return renderError();
    switch (view) {
      case View.Data:
        return <DataView workflow={workflow} />;
      case View.Timeline:
        return <TimelineView workflow={workflow} workflowId={workflowId} />;
      case View.State:
        return <StateView workflow={workflow} />;
      // case View.Dependents:
      //   return <DependentsView workflow={workflow} workflowId={workflowId} />;
      case View.Status:
        return <StatusView workflow={workflow} workflowId={workflowId} />;
      default:
        return <DataView workflow={workflow} />;
    }
  };

  const renderBackLink = () => {
    const parentKey = workflow?.state?.metadata?.pj || workflow?.state?.metadata?.key;
    if (!parentKey) return null;
    const queryParams = new URLSearchParams(window.location.search);
    const app = workflow?.state?.metadata?.app;
    const jobId = `hmsh:${app}:j:${parentKey}`;    
    return (
      <Link 
        title="View Parent Workflow" 
        onClick={() => navigate(`/workflows/${jobId}?${queryParams.toString()}`, { replace: false })}
        style={{ cursor: 'pointer', color: theme.palette.primary.light }}>
        ../
      </Link>
    );
  };

  const getViewTitle = () => {
    if (error) return `Workflow`;
    switch (view) {
      case View.Data: return "Workflow Data";
      case View.Timeline: return "Workflow History";
      case View.State: return "Workflow State";
      case View.Status: return "Workflow Status";
      // case View.Dependents: return "Child Workflows";
      default: return "";
    }
  };

  const isViewSelected = (currentView: View) => view === currentView ? 'primary' : 'default';

  return shouldDisplay ? (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        padding: '1em',
        height: '100%',
        opacity: opacity,
        transition: 'opacity 500ms ease-out, height 500ms ease-out',
        position: 'relative',
      }}
    >
      <Box sx={{ display: 'flex', paddingTop: '.25em'}}>
        <Box sx={{ minWidth: 180, maxWidth: 180 }}>
          <Typography variant="h6" component="div" style={{ fontSize: '1.15em', marginTop: '.25em' }}>
            {getViewTitle()}
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', flexGrow: 1, justifyContent: 'center' }}>
          <Typography variant="h6" component="div" style={{ lineHeight: '1em', padding: '.25em', fontSize: '1.15em', cursor: 'pointer', marginTop: '.25em', whiteSpace: 'nowrap' }} onClick={onRefresh}>
            {renderBackLink()} {truncateWithEllipsis(workflowId?.split(':')[3], 32)}
          </Typography>
        </Box>
        <Box sx={{ minWidth: 180, maxWidth: 180, display: 'flex', justifyContent: 'flex-end' }}> {/* Box 3 */}
          <IconButton title="Workflow Data" onClick={() => setView(View.Data)} color={isViewSelected(View.Data)}><DataObjectIcon /></IconButton>
          <IconButton title="Workflow History" onClick={() => setView(View.Timeline)} color={isViewSelected(View.Timeline)}><TimelineIcon /></IconButton>
          <IconButton title="Workflow State" onClick={() => setView(View.State)} color={isViewSelected(View.State)}><StateIcon /></IconButton>
          <IconButton title="Workflow Status" onClick={() => setView(View.Status)} color={isViewSelected(View.Status)}><StatusIcon /></IconButton>
          {/* <IconButton title="Child Workflows" onClick={() => setView(View.Dependents)} color={isViewSelected(View.Dependents)}><DependenciesIcon /></IconButton> */}
        </Box>
      </Box>

      <Box sx={{ flex: 1, marginTop: '1em', overflowY: 'auto' }}>
        {renderView()}
      </Box>
    </Box>
  ) : null;
};

export default Workflow;
