// src/services/rpc.ts

import {
  ChatMessage,
  DurableJobExport,
  FindJobsOptions,
  FindOptions,
  FindWhereOptions,
  QuorumProfile,
  RollCallOptions,
  SearchResults, 
  ThrottleOptions,
  ManifestProfiles } from '../types/meshdata';

const BASE_URL = window.location.href.startsWith('http://localhost:3000') ? 'http://localhost:3010/api/v1/meshdata' : '/api/v1/meshdata';

// Helper function to abstract the fetch call
async function _fetch(methodName: string, bodyData: object, headers: HeadersInit = { 'Content-Type': 'application/json' }): Promise<any> {
  try {
    const response = await fetch(`${BASE_URL}/${methodName}`, {
      method: 'POST',
      headers,
      body: JSON.stringify(bodyData),
    });
    if (!response.ok) throw new Error(`RPC Error [${methodName}]: ${response.status} ${response.statusText}`);
    return await response.json();
  } catch (error) {
    console.error(`RPC Error [${methodName}]`, error);
    throw error;
  }
}

export async function settings(headers: HeadersInit = { 'Content-Type': 'application/json' }): Promise<any> {
  try {
    const response = await fetch(`${BASE_URL}/settings`, { headers });
    if (!response.ok) throw new Error(`SETTINGs Error: ${response.status} ${response.statusText}`);
    return await response.json();
  } catch (error) {
    console.error(`SETTINGs Error`, error);
    throw error;
  }
}

/**
 * Sends a signal to the backend Service Mesh (workers and engines)
 * to announce their presence, including message counts, target
 * functions, topics, etc. This is useful for establishing
 * the network profile and overall message throughput
 * of the operational data layer as a unified quorum.
 * @param {RollCallOptions} options 
 * @returns {Promise<HotMeshTypes.QuorumProfile[]>}
 */
export const rollCall = async (options: RollCallOptions = {}, metadata: {database: string, namespace: string}): Promise<QuorumProfile[]> => {
    return _fetch('rollCall', { data: options ? [options] : [], metadata });
};

/**
 * Exports the job profile for the function execution, including
 * all state, process, and timeline data. The information in the export
 * is sufficient to capture the full state of the function in the moment
 * and over time.
 * 
 * @param {string} entity - the entity name (e.g, 'user', 'order', 'product')
 * @param {string} id - The workflow/job id
 * 
 * @example
 * // Export a function
 * await meshdata.exportJob('greeting', 'jsmith123');
 */
export const exportJob = async (entity: string, id: string, options = {}, namespace: string, database: string): Promise<DurableJobExport> => {
    return _fetch('export', { data: [entity, id, options, namespace], metadata: { entity, namespace, database } });
};

/**
 * Provides a JSON abstraction for the Redis FT.search command
 * (e.g, `count`, `query`, `return`, `limit`)
 * NOTE: If the type is TAG for an entity, `.`, `@`, and `-` must be escaped.
 * 
 * @param {string} entity - the entity name (e.g, 'user', 'order', 'product')
 * @param {FindWhereOptions} options - find options (the query)
 * @param {string} namespace - the namespace for the entity
 * @returns {Promise<SearchResults | number>} Returns a number if `count` is true, otherwise a SearchResults object.
 * @example
 * const results = await meshdata.findWhere('greeting', {
 *  query: [
 *   { field: 'name', is: '=', value: 'John' },
 *   { field: 'age', is: '>', value: 2 },
 *   { field: 'quantity', is: '[]', value: [89, 89] }
 *  ],
 *  count: false,
 *  limit: { start: 0, size: 10 },
 *  return: ['name', 'quantity']
 * });
 * 
 * // returns { count: 1, query: 'FT.SEARCH my-index @_name:"John" @_age:[2 +inf] @_quantity:[89 89] LIMIT 0 10', data: [ { name: 'John', quantity: '89' } ] }
 */
export const findWhere = async (entity: string, options: FindWhereOptions, namespace: string, database: string): Promise<SearchResults | number> => {
  try {
    return await _fetch('findWhere', { data: [entity, options], metadata: { entity, namespace, database } });
  } catch (error: any) {
    console.error("FTS Error:", error.message);
    return { count: 0, query: 'FT.SEARCH UNSUPPORTED', data: [] };
  }
};

/**
   * Executes the redis FT search query; optionally specify other commands
   * @param {string} entity
   * @param {FindOptions} options
   * @param {any[]} args
   * @returns {Promise<string[] | [number] | Array<number, string | number | string[]>>}
   * @example '@_quantity:[89 89]'
   * @example '@_quantity:[89 89] @_name:"John"'
   * @example 'FT.search my-index @_quantity:[89 89]'
   */
export const find = async(entity: string = 'user', options: FindOptions, ...args: string[]): Promise<string[] | [number] | Array<string | number | string[]>> => {    
  return _fetch('find', { data: [entity, options, ...args], metadata: { database: options.database, namespace: options.namespace, entity } });
  //[count, [id, fields[]], [id, fields[]], [id, fields[]], ...]]
}

/**
 * Throttles a worker or engine in the backend Service Mesh, using either
 * a 'guid' to target a specific worker/engine combo, or a 'topic' to target
 * a group of worker(s) connected to the topic. The throttle value is
 * specified in milliseconds and will cause the target(s) to delay consuming
 * the next message by this amount. By default, the value is set to `0`.
 * @param {ThrottleOptions} options
 * @returns {Promise<boolean>}
 * 
 * @example
 * // Throttle a worker or engine
 * await meshdata.throttle({ guid: '1234567890', throttle: 10_000 });
 */
export const throttle = async (options: ThrottleOptions, namespace: string, database: string): Promise<boolean> => {
  return _fetch('throttle', { data: [options], metadata: { namespace, database } });
}

/**
 * Lists all search indexes in the operational data layer when the
 * targeted Redis backend supports the FT module.
 * @returns {Promise<string[]>}
 * @example
 * // list all search indexes
 * const indexes = await meshdata.listSearchIndexes();
 * 
 * // returns ['greeting', 'user', 'order', 'product']
 */
export const  listSearchIndexes = async (namespace: string, database: string): Promise<string[]> => {
  return _fetch('listSearchIndexes', {namespace, database});
}

/**
 * Retrieves the dashboard manifest, describing all databases, namespaces,
 * and entities in the operational data layer, organized as a tree. The
 * dashboard UI traverses this tree to generate the navigation controls and
 * display the targeted data.
 * @returns 
 */
export const  getManifest = async (): Promise<ManifestProfiles> => {
  return _fetch('getManifest', {});
}

/**
 * For those Redis implementations without the FT module, this quasi-equivalent
 * method is provided that uses SCAN along with a custom match
 * string to view jobs. A cursor is likewise provided for rudimentary
 * forward-pagination.
 * @param {FindJobsOptions} [options]
 * @param {object} metadata
 * @returns {Promise<[string, string[]]>}
 * @example
 * // find jobs
 * const [cursor, jobs] = await meshdata.findJobs({ match: 'greeting*' });
 * 
 * // returns [ '0', [ 'hmsh:durable:j:greeting-jsmith123', 'hmsh:durable:j:greeting-jdoe456' ] ]
 */
export const findJobs = async (options: FindJobsOptions, metadata: { namespace: string, entity: string, database: string }): Promise<[string, string[]]> => {
  return _fetch('findJobs', { data: [options], metadata });
};

/**
 * Sends a free-form message to the backend Service Mesh, which will be processed
 * by the LLM to generate a structured FT.Search query
 * @param messages 
 * @param metadata 
 * @returns 
 */
export const ask = async (messages: { role: 'user' | 'assistant', content: string}[], metadata: { namespace: string, entity: string, database: string }): Promise<ChatMessage> => {
  return _fetch('ask', { data: { messages }, metadata});
};
