import moment from 'moment';
import React, { useState, useEffect, useRef } from 'react';
import { Grid, Box, Card, CardContent, Typography, TextField, Button, List, ListItemButton, IconButton, useTheme, CircularProgress } from '@mui/material';
import NewConversationIcon from '@mui/icons-material/AddCommentOutlined';
import EditIcon from '@mui/icons-material/EditOutlined';
import DeleteIcon from '@mui/icons-material/DeleteOutlined';
import InfoIcon from '@mui/icons-material/InfoOutlined';
import SearchIcon from '@mui/icons-material/ManageSearchOutlined';

import usePersistentDataStore from '../hooks/usePersistentDataStore';
import brandLogo from '../img/hotmesh_144_v.png';
import { ChatSession, ChatMessage } from '../types/meshdata';
import { ask } from '../services/rpc';
import { terminalToString } from '../modules/utils';
import { useSearchParams } from 'react-router-dom';

const Commander = ({ database, namespace, onExecChat }: { database: string, namespace: string, onExecChat: (ftQuery: string, nlQuery: string) => void }) => {
  const theme = useTheme();
  const [inputMessage, setInputMessage] = useState('');
  const [activeSessionDetails, setActiveSessionDetails] = useState<string[] | null>();
  const { items: chatSessions, addItem, updateItem, removeItem } = usePersistentDataStore<ChatSession>('ChatSessions');
  const [activeSessionId, setActiveSessionId] = useState<string | null>(null);
  const [activeChatSession, setActiveChatSession] = useState<ChatSession | null>(null);
  const [editSessionId, setEditSessionId] = useState<string | null>(null); // Track session being edited
  const messagesEndRef = useRef<HTMLElement | null>(null);
  const [isAsking, setIsAsking] = useState(false);
  const [searchParams] = useSearchParams();


  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [activeChatSession?.messages]);

  useEffect(() => {
    setActiveSessionDetails(null);
    if (activeSessionId) {
      const session = chatSessions.find(session => session.id === activeSessionId);
      setActiveChatSession(session || null);
    } else if(chatSessions?.length) {
      setActiveSessionId(chatSessions[0].id)
    }
  }, [activeSessionId, chatSessions]);

  const sleep = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

  const createNewSession = (): ChatSession => {
    const newSession: ChatSession = {
      id: Date.now().toString(),
      title: `Assistant Session ${chatSessions.length + 1}`, // Default title
      messages: [],
    };
    addItem(newSession);
    setActiveSessionId(newSession.id);
    return newSession;
  };

  const sendMessage = async (e: React.FormEvent<HTMLFormElement> | null, newSession?: ChatSession) => {
    e?.preventDefault();
    let sessionToUse = activeChatSession;
  
    if (inputMessage.trim() === '') return;

    if (!chatSessions.length && !newSession) {
      newSession = createNewSession();
      setActiveSessionId(newSession.id);
      sessionToUse = newSession;
    }
  
    if (!sessionToUse) return;
  
    let timestamp = new Date().toISOString();
    let newMessage: ChatMessage = { timestamp, message: inputMessage, role: 'user' };
    let updatedSession: ChatSession = {
      ...sessionToUse,
      messages: [...sessionToUse.messages, newMessage],
    };

    setInputMessage(''); // Clear the input field after sending a message
    updateItem(updatedSession); // Assuming updateItem method handles the persistence
    setIsAsking(true);
    // map the updatedSession.messages to create the array of messages to send to the ask function
    const toAsk = updatedSession.messages.map((msg) => {
      const role: "user" | "assistant" = msg.role === 'user' ? 'user' : 'assistant';
      return { role, content: msg.message || JSON.stringify({ prose: msg.prose, ft_query: msg.ft_query }) };
    });
    const answer = await ask(toAsk, { namespace, database, entity: searchParams.get('entity') || '*' });
    setIsAsking(false);

    await sleep(1_500);

    timestamp = new Date().toISOString();
    updatedSession = {
      ...updatedSession,
      messages: [...updatedSession.messages, { ...answer, timestamp }],
    };

    updateItem(updatedSession); // Assuming updateItem method handles the persistence
  };  

  const handleTitleChange = (id: string, title: string) => {
    const updatedSession = chatSessions.find(session => session.id === id);
    if (updatedSession) {
      updatedSession.title = title;
      updateItem(updatedSession);
      setEditSessionId(null); // Exit editing mode
    }
  };

  // Add a deleteSession function
  const deleteSession = (id: string) => {
    removeItem(id);
    if (activeSessionId === id) {
      setActiveSessionId(null);
      setActiveChatSession(null);
    }
  };

  const renderUser = (msg: ChatMessage) => {
    return msg.message;
  };

  const renderAssistant = (msg: ChatMessage) => {
    if (msg.question) {
      return msg.question;
    } else if (msg.message) {
      return msg.message
    } else if (msg.prose) {
      return (
        <Box>
          <Box>
            Here's what I understood from your question: 
          </Box>
          <Box style={{ flexGrow: 1 }}>
            <pre style={{
              whiteSpace: 'pre-wrap',
              padding: '1em',
              backgroundColor: theme.palette.action.hover,
              borderRadius: '.125em'
            }}>
              {msg.prose}
            </pre>
          </Box>
        </Box>
      )
    }
  };

  const onDelete = (index: number) => {
    const targetText = activeChatSession?.messages[index].message;
    const updatedSession: ChatSession = {
      ...activeChatSession,
      messages: activeChatSession?.messages.slice(0, index) || [],
      id: activeChatSession?.id || '', // Provide a default value of an empty string if id is undefined
      title: activeChatSession?.title || '', // Provide a default value of an empty string if title is undefined
    };
    updateItem(updatedSession);
    setInputMessage(targetText as string);
  };

  return (
      <Grid container sx={{
        flexGrow: 1,
        p: 2,
        height: 'calc(100% - 48px)',
        overflow: 'hidden' }}
      >
        <Grid item xs={3} sx={{ pr: 2, display: 'flex', flexDirection: 'column', height: '100%' }}>
          <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between' }}>
            <img
              src={brandLogo}
              alt="Brand Logo" 
              style={{ cursor: 'pointer', height: '1.75em', position: 'relative', left: '5px', top: '-3px' }}
            />
            <Typography variant="h6" sx={{flexGrow: 1}}>HotMesh Query Assistant</Typography>
            <IconButton style={{
              visibility: chatSessions.length ? 'visible': 'hidden',
              }} onClick={createNewSession}><NewConversationIcon /></IconButton>
          </Box>
          <Box sx={{ flexGrow: 1, overflowY: 'auto' }}>
            <List>
              {chatSessions.sort((a, b) => b.id.localeCompare(a.id)).map((session: ChatSession) => (  
                <ListItemButton key={session.id}
                  onClick={() => setActiveSessionId(session.id)} 
                  sx={{
                    display: 'flex',
                    lineHeight: '0',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    backgroundColor: activeSessionId === session.id ? theme.palette.action.focus : 'transparent',
                    padding: '0 .25em',
                    '& .MuiIconButton-root': { // Target IconButton within ListItemButton
                      p: '2px', // Reduce padding around the icon buttons for a more compact look
                    },
                    '& .MuiTextField-root': { // Target TextField within ListItemButton
                      m: 0, // Remove margins around the text field
                      '& .MuiInputBase-input': { // Target the input element itself for further size adjustments if needed
                        py: '2px', // Adjust padding if necessary to make the text field more compact
                      },
                    },
                  }}
                >
                  {editSessionId === session.id ? (
                    <TextField
                      autoFocus
                      defaultValue={session.title}
                      onBlur={(e) => handleTitleChange(session.id, (e.target as HTMLInputElement).value)}
                      onKeyDown={(e) => e.key === 'Enter' && handleTitleChange(session.id, (e.target as HTMLInputElement).value)}
                      size="small"
                    />
                  ) : (
                    <Typography noWrap sx={{ flexGrow: 1, mt: '.5em', mb: '.5em' }}>
                      {session.title || `Session ${session.id}`}
                    </Typography>
                  )}
                  <IconButton size="small" onClick={() => setEditSessionId(session.id)} sx={{ mx: 1 }}>
                    <EditIcon fontSize="inherit" />
                  </IconButton>
                  <IconButton size="small" onClick={() => deleteSession(session.id)}>
                    <DeleteIcon fontSize="inherit" />
                  </IconButton>
                </ListItemButton>
              ))}
            </List>            
          </Box>
        </Grid>
        <Grid item xs={6} sx={{
          display: 'flex',
          flexDirection: 'column',
          height: '100%',
          p: 2,
          border: 'solid 1px',
          borderColor: theme.palette.action.selected,
          borderRadius: '4px'
        }}>
          <Box sx={{ flexGrow: 1, overflowY: 'auto', pr: 2 }}>
            {activeChatSession && activeChatSession.messages.map((msg, index) => (
              <Card key={index} sx={{
                mb: 2,
                border: 'solid 1px',
                borderColor: msg.role === 'user' ? theme.palette.action.selected : theme.palette.action.hover,      
                textAlign: msg.role === 'user' ? 'right' : 'left',
                marginLeft: msg.role === 'user' ? '20%' : '',
                marginRight: msg.role !== 'user' ? '20%' : '',
                bgcolor: msg.role === 'user' ? theme.palette.action.selected : theme.palette.action.hover,
              }}>
                <CardContent>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      {msg.role !== 'user' && <img src={brandLogo} alt="Brand Logo" style={{ width: '1em', marginRight: '5px', marginTop: '-5px' }} />}
                      <Typography color="text.secondary" sx={{ fontSize: '.75em', flexGrow: 1 }}>
                        {moment(msg.timestamp).format("MMMM DD, YYYY, h:mm:ss A")}
                      </Typography>
                    </Box>
                    <Box>
                      {msg.role !== 'user' && <IconButton size="small" onClick={() => setActiveSessionDetails(msg.ft_query)}><InfoIcon fontSize="inherit" /></IconButton>}
                      {msg.role !== 'user' && <IconButton size="small" onClick={() => onExecChat(terminalToString(msg.ft_query || []), msg.prose || '')}><SearchIcon fontSize="inherit" /></IconButton>}
                      {msg.role === 'user' && <IconButton size="small" onClick={() => onDelete(index)}><DeleteIcon fontSize="inherit" /></IconButton>}
                    </Box>
                  </Box>
                  <Typography component="div" sx={{ mt: 1 }}>{msg.role === 'user' ? renderUser(msg) : renderAssistant(msg)}</Typography>
                </CardContent>
              </Card>
            ))}
            <div ref={messagesEndRef as React.RefObject<HTMLDivElement>} style={{textAlign: 'center', width: '100%'}}>
              <CircularProgress variant="indeterminate" value={70} style={{
                visibility: isAsking ? 'inherit' : 'hidden',
              }} />
            </div>
          </Box>
          <Box component="form" onSubmit={sendMessage} sx={{
            display: 'flex',
            mt: 2,
            pr:2,
            pl: 2,
            visibility: chatSessions.length && !activeSessionId ? 'hidden' : 'inherit',
          }}>
            <TextField
              fullWidth
              autoFocus
              disabled={isAsking}
              variant="outlined"
              label="What are you looking for?"
              value={inputMessage}
              onChange={(e) => setInputMessage(e.target.value)}
              sx={{ 
                mr: 1,
               }}
            />
            <Button variant='contained' type="submit" disabled={isAsking}>
              Send
            </Button>
          </Box>
        </Grid>
        <Grid item xs={3} sx={{
          display: 'flex',
          flexDirection: 'column',
          height: '100%',
          p: 2,
          pt: 0,
        }}>
          <Typography variant="h6" style={{visibility: activeSessionDetails ? 'inherit' : 'hidden'}}>Query / Syntax Details</Typography>
          <Box style={{display: 'flex', flexWrap: 'wrap', overflow: 'auto', fontSize: '.75em'}}>
            {activeSessionDetails ? activeSessionDetails.map((token: string, index: number) => {
              return <span key={index}><span style={{
                backgroundColor: theme.palette.primary.light,
                color: theme.palette.primary.contrastText,
                borderRadius: '.25em',
                display: 'inline-block',
                margin: '.25em',
                padding: '.25em',
                whiteSpace: 'nowrap',
                wordBreak: 'keep-all',
              }}>{token}</span>&nbsp;</span>
            }) : null}
          </Box>
        </Grid>
      </Grid>
  );
}

export default Commander;
