import { SetStateAction, useEffect, useRef, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Chip, Drawer, Grid, List, ListItem, ListItemButton, ListItemText, Typography, Box, useTheme, Divider, Select, MenuItem, Icon, IconButton } from '@mui/material';
import SettingsIcon from '@mui/icons-material/Settings';
import HookIcon from '@mui/icons-material/PhishingOutlined';
import EntityIcon from '@mui/icons-material/LandscapeOutlined';
import CalendarIcon from '@mui/icons-material/CalendarMonthOutlined';
import StopIcon from '@mui/icons-material/StopCircleOutlined';
import PlayIcon from '@mui/icons-material/PlayCircleOutlined';
import moment from 'moment';

import { rollCall, throttle } from '../services/rpc';
import { QuorumProfile } from '../types/meshdata';
import Connection from './Connection';
import { useNamespace } from '../context/NamespaceContext';

const Connections = () => {
  const MAX_THROTTLE = 2147483647;
  const theme = useTheme();
  let { connection_id } = useParams();
  const hasRollCalled = useRef(false);
  const navigate = useNavigate();
  const [connections, setConnections] = useState<QuorumProfile[]>([]);
  const [selectedItemId, setSelectedItemId] = useState<string | null>(null);
  const [currentThrottle, setCurrentThrottle] = useState<number>(0);
  const [engineCount, setEngineCount] = useState<number>(0);
  const [selectedItem, setSelectedItem] = useState<QuorumProfile | null>(null);
  const [prior, setPrior] = useState<string>('');
  const { namespace, database } = useNamespace();
  const [reload, setReload] = useState(false); // trigger data reload
  const [connectionType, setConnectionType] = useState('workers');
  
  useEffect(() => {
    const fetchAndSortConnections = async () => {
      if (connection_id && hasRollCalled.current) return;
      try {
        const response = await rollCall({ namespace }, { namespace, database });
        setConnections(prevConnections => {
          const newConnectionsMap = new Map(prevConnections.map(item => [`${item.engine_id}${item.worker_topic}`, item]));
          let engineCount = 0;
          response.forEach(item => {
            if (item.throttle === MAX_THROTTLE) item.throttle = -1;
            if (item.worker_topic) {
              const parts = item.worker_topic.split('-');
              let topicOrEngineId: string;
              if (parts.length === 3) {
                if (parts[0] !== parts[1]) {
                  topicOrEngineId = `${parts[1]} ${parts[2]} (${parts[0]})`;
                } else  {
                  topicOrEngineId = `${parts[1]} ${parts[2]}`;
                }
              } else if (parts.length === 2 && parts[0] === parts[1]) {
                topicOrEngineId = parts[0];
              } else {
                topicOrEngineId = `${parts[1]} (${parts[0]})`;
              }
              const what = topicOrEngineId.indexOf('.') > -1 ? (<HookIcon style={{verticalAlign: 'middle', marginRight: '.5em' }} />) : <EntityIcon style={{verticalAlign: 'middle', marginRight: '.5em' }}  />;
              item.$label = topicOrEngineId;
              item.$type = what;
            } else {
              engineCount++;
              item.$label = item.engine_id;
              item.$type = <SettingsIcon style={{verticalAlign: 'middle', marginRight: '.5em' }} />;
            }
            const key = `${item.engine_id}${item.worker_topic}`;
            if (newConnectionsMap.has(key)) {
              Object.assign(newConnectionsMap.get(key) || {}, item);
            } else {
              newConnectionsMap.set(key, item);
            }
          });
          setEngineCount(engineCount);
          return Array.from(newConnectionsMap.values());
        });
      } catch (error) {
        console.error('Error fetching connections', error);
        setConnections([]);
      }
    };

    if (!hasRollCalled.current || (namespace !== prior)) {
      if (!namespace) {
        setConnections([]);
      }
      if (namespace !== prior) {
        setPrior(namespace);
        setConnections([]);
      }
      fetchAndSortConnections();
      hasRollCalled.current = true;
    }
    const interval = setInterval(fetchAndSortConnections, 10_000);
    return () => clearInterval(interval);
  }, [namespace, reload, connection_id]);

  useEffect(() => {
    const handleLocationChange = () => {
      if (connection_id) {
        const item = connections.find(item => connection_id === `id:${item.engine_id}` || connection_id === `topic:${item.worker_topic}`);
        if (item) {
          setSelectedItemId(connection_id);
          setSelectedItem(item);
          setCurrentThrottle(item.throttle ?? 0);
        }
      } else {
        setSelectedItem(null);
      }
    };

    handleLocationChange();
  }, [connections, connection_id, namespace]);

  const handleSelect = (item: QuorumProfile) => {
    const id = item.worker_topic ? `topic:${item.worker_topic}` : `id:${item.engine_id}`;
    navigate(`/connections/${id}`);
    setSelectedItemId(id);
    setSelectedItem(item);
    setCurrentThrottle(item.throttle ?? 0);
  };

  /**
   * Update throttle value
   */
  const updateThrottle = async (throttleValue: number | string, guid?:string, topic?: string | null) => {
    if (selectedItem) {
      if (Number(throttleValue) === -1) {
        throttleValue = MAX_THROTTLE;
      }
      await throttle({ guid, throttle: throttleValue as number, topic, namespace }, namespace, database);
      setPrior('');
      navigate(`/connections`, { replace: false });
      setReload(!reload);
    }
  };

  const sortByEngineIdAndTopic = (a: QuorumProfile, b: QuorumProfile) => {
    if (a.worker_topic && b.worker_topic) {
      return a.worker_topic.localeCompare(b.worker_topic);
    } else if (a.worker_topic) {
      return 1;
    } else if (b.worker_topic) {
      return -1;
    } else {
      return a.engine_id.localeCompare(b.engine_id);
    }
  }

  const hydrateDate = (date: string) => {
    const engineInited = moment(date, 'YYYYMMDDHHmmss.SSS');
    return (
      <>
        <CalendarIcon
          style={{verticalAlign: 'middle', marginRight: '1em', marginLeft: '.25em', fontSize: '1rem'}}
        />
        {engineInited.format('MMMM Do YYYY, h:mm:ss a')}
      </>
    );
  }

  const DrawerSet = () => {
    return (
      <Drawer
        anchor="right"
        open={!window.location.pathname.endsWith('/connections')}
        onClose={() => navigate(`/connections`, { replace: false })}
        PaperProps={{
          sx: { borderTopLeftRadius: 8, borderTopRightRadius: 8 }
        }}
      >
        <Connection
          connectionType={connectionType}
          selectedItem={selectedItem}
          throttleValue={currentThrottle}
          updateThrottle={(opts: { guid?: string; topic?: string | null; throttle: number | string }) => updateThrottle(opts.throttle, opts.guid, opts.topic)}
        />
      </Drawer>
    );
  }

  const renderThroughput = (item: QuorumProfile) => {
    const resolveLabel = (throttle: number) => {
      if (throttle === -1) return '+inf';
      if (throttle === 0) return '0';
      return (throttle / 1000).toLocaleString('en-US', { minimumFractionDigits: 0, maximumFractionDigits: 2 });
    }
    return (
      <div style={{minWidth: '180px', textAlign: 'left'}}>
        <Typography variant="body1" component="div" style={{fontSize: '.85em', marginTop: '.5em'}}>
          Stream Throttle (seconds)
        </Typography>
        <Divider style={{marginBottom: '.5em'}}/>
        <Typography
          variant="body2"
          component="div"
          style={{fontSize: '.75em', lineHeight: '.75em', whiteSpace: 'nowrap', textAlign: 'left'}}>
          <Chip
            label={resolveLabel(item?.throttle ?? 0)}
            color={item?.throttle ? (item.throttle === -1 ? 'error' :'warning') : 'success'}
            variant="outlined"
            sx={{borderRadius: '2px', lineHeight: 'normal'}}
            size="small" />
        </Typography>
      </div>
    );
  };

  const renderStreamDepth = (item: QuorumProfile) => {
    return (
      <div style={{ marginLeft: '40px', minWidth: '180px', textAlign: 'left' }}>
        <Typography variant="body1" component="div" style={{fontSize: '.85em', marginTop: '.5em'}}>
          Stream Depth
        </Typography>
        <Divider style={{marginBottom: '.5em'}}/>
        <Typography
          variant="body2"
          component="div"
          style={{fontSize: '.75em', lineHeight: '.75em', whiteSpace: 'nowrap', textAlign: 'left'}}>
          <Chip
            label={(item.stream_depth ?? 0).toLocaleString()}
            sx={{borderRadius: '2px', lineHeight: 'normal'}}
            variant="outlined"
            color={item?.stream_depth ? 'error' : 'success'}
            size="small" />
        </Typography>
      </div>
    );
  };

  const renderEngineDepth = (connections: QuorumProfile[] | undefined) => {
    if (!['engines','both'].includes(connectionType)) return null;
    const item = connections?.find((item) => !item.worker_topic && Number(item.stream_depth) > 0);
    if (!item?.stream_depth) return null;
    return (
      <Chip
        label={`Backlog ${(item?.stream_depth ?? 0).toLocaleString()}`}
        sx={{borderRadius: '2px', lineHeight: 'normal'}}
        variant="outlined"
        color="error"
        size="small" />
    );
  };

  const renderCounts = (counts?: Record<string, number>) => {
    return counts ? (
      <div style={{ textAlign: 'right', margin: '0 0 0 2em', minWidth: '180px' }}>
        <Typography
          variant="body1"
          component="div"
          style={{fontSize: '.85em', whiteSpace: 'nowrap', marginTop: '.5em', minWidth: '120px', textAlign: 'left'}}>
          Historical Volume
        </Typography>
        <Divider style={{marginBottom: '.5em'}}/>
        {Object.entries(counts).map(([status, count]) => (
          <Typography
            key={status}
            variant="body2"
            component="div"
            style={{fontSize: '.75em', marginBottom: '.25em', whiteSpace: 'nowrap', textAlign: 'left'}}>
            <Chip 
              label={count.toLocaleString()}
              sx={{borderRadius: '2px', lineHeight: 'normal'}}
              variant="outlined"
              color="success"
              size="small" />
            <span style={{float: 'right', textAlign: 'right', marginTop: '.5em'}}>{status}</span>
          </Typography>
        ))}
      </div>
    ) : null;
  };

  const renderListItem = (item: QuorumProfile, isFiltered: boolean = false) => {
    const isSelected = selectedItemId === `id:${item.engine_id}` || selectedItemId === `topic:${item.worker_topic}`;
    return (
      <ListItemButton
        onClick={!isFiltered ? () => handleSelect(item) : undefined}
        selected={isSelected}
        key={`${item.engine_id}${item.worker_topic}`}
        style={{
          backgroundColor: isSelected ? theme.palette.action.selected : 'transparent',
          marginLeft: '1em'
        }}
        alignItems="flex-start"
      >
        <Grid container spacing={2} wrap="nowrap">
          <Grid item sm>
            <ListItemText
              primary={
                <Box sx= {{display: 'flex', flexDirection: 'row', alignItems: 'right', pr: '1em'}}>
                  {item.$type}
                  {<Box style={{display: 'inline-block', minWidth: '300px'}}>{item?.worker_topic ?? item?.$label}</Box>}
                </Box>
              }
              primaryTypographyProps={{ style: { fontSize: '1.15em', paddingTop: '0' } }}
              secondaryTypographyProps={{ component: 'div' }}
              secondary={
                <>
                  {item.worker_topic && 
                    <div style={{fontSize: '.75em', display: 'block', marginTop: '.5em', marginLeft: '3.5em', marginBottom: '.5em'}}>{item.engine_id}</div>}
                 {item.inited && <Typography variant="body2" component="div" style={{fontSize: '.85em', marginBottom: '.75em'}}>{hydrateDate(item.inited)}</Typography>}
                </>
              }
            />
          </Grid>
          <Grid item>
            {renderThroughput(item)}
          </Grid>
          {connectionType !== 'engines' ? <Grid item>
            {renderStreamDepth(item)}
          </Grid> : null}
          <Grid item>
            {renderCounts(item.counts)}
          </Grid>
        </Grid>
      </ListItemButton>
    );
  };

  const playAll = async () => {
    await throttle({ throttle: 0, namespace }, namespace, database);
    setTimeout(() => {
      rollCall({ namespace }, { namespace, database });
    }, 500);
  };

  const pauseAll = async () => {
    await throttle({ throttle: -1, namespace }, namespace, database);
    setTimeout(() => {
      rollCall({ namespace }, { namespace, database });
    }, 500);
  }

  return (
    <Grid sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <Box sx={{
        padding: theme.spacing(2),
        borderBottom: `1px solid ${theme.palette.divider}`,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        backgroundColor: theme.palette.background.default,
        position: 'sticky',
        top: 0,
        zIndex: 1,
      }}>
        <Typography variant="h5">Stream Connections
          <Chip
            title="Total count (click to refresh)"
            onClick={() => setReload(!reload)}
            variant="outlined"
            style={{
              cursor: 'pointer',
              paddingTop: '0.25em',
              marginLeft: '1em',
              display: 'inline-block',
              borderRadius: '2px',
              backgroundColor: theme.palette.primary.main,
              color: theme.palette.primary.contrastText,
            }}
            label={`Total ${connections?.length || 0}`}
            color="default"
            size="small" />
          <IconButton title="Resume all streams (remove all throttles)" sx={{ml: '.5em', color: theme.palette.success.main}} onClick={playAll}><PlayIcon /></IconButton>
          <IconButton title="Pause all streams (apply infinite throttle)" sx={{color: theme.palette.error.main}} onClick={pauseAll}><StopIcon /></IconButton>
        </Typography>
        {renderEngineDepth(connections)}
        <Select
          value={connectionType}
          onChange={(event: { target: { value: SetStateAction<string>; }; }) => setConnectionType(event.target.value)}
          displayEmpty
          inputProps={{ 'aria-label': 'View engines or workers' }}
          style={{
            height: '2.5rem',
          }}
        >
          <MenuItem key="workers" value="workers">Workers ({(connections?.length ?? 0) - engineCount})</MenuItem>
          <MenuItem key="engines" value="engines">Engines ({engineCount})</MenuItem>
        </Select>
      </Box>
      <Box>
        <DrawerSet />
        <List component="nav" sx={{ overflowY: 'auto', flexGrow: 1 }}>
          {connections.some((item) => item.worker_topic && ['workers','both'].includes(connectionType)) && (
            <>
              {['both'].includes(connectionType) && <ListItem>
                <Typography variant="h6" style={{fontStyle: 'italic'}}>Workers</Typography>
              </ListItem>}
              {connections.filter((item) => item.worker_topic).sort(sortByEngineIdAndTopic).map((item) => renderListItem(item, false))}
            </>
          )}
          {connections.some((item) => !item.worker_topic && ['engines','both'].includes(connectionType)) && (
            <>
              {['both'].includes(connectionType) && <ListItem>
                <Typography variant="h6" style={{fontStyle: 'italic'}}>Engines</Typography>
              </ListItem>}
              {connections.filter((item) => !item.worker_topic).sort(sortByEngineIdAndTopic).map((item) => renderListItem(item, false))}
            </>
          )}
        </List>
      </Box>
    </Grid>
  );
};

export default Connections;
