// src/hooks/usePersistentDataStore.ts
import { useEffect, useState } from 'react';

interface StoreItem {
  id: string | number; // Unique identifier for each item
  [key: string]: any; // Allow for arbitrary properties on each item
}

// A hook that manages a persistent array of objects
const usePersistentDataStore = <T extends StoreItem>(storeName: string) => {
  // State to hold the array of items
  const [items, setItems] = useState<T[]>(() => {
    // Attempt to load existing items from localStorage
    const savedItems = localStorage.getItem(storeName);
    return savedItems ? JSON.parse(savedItems) : [];
  });

  // Update a specific item by id
  const updateItem = (updatedItem: T, upsert = false) => {
    setItems(prevItems => {
      const itemIndex = prevItems.findIndex(item => item.id === updatedItem.id);
      if (itemIndex !== -1) {
        const newItems = [...prevItems];
        newItems[itemIndex] = updatedItem;
        localStorage.setItem(storeName, JSON.stringify(newItems));
        return newItems;
      } else if (upsert) {
        const newItems = [...prevItems, updatedItem];
        localStorage.setItem(storeName, JSON.stringify(newItems));
        return newItems;
      }
      return prevItems;
    });
  };

  // Add a new item
  const addItem = (newItem: T) => {
    setItems(prevItems => {
      const newItems = [...prevItems, newItem];
      localStorage.setItem(storeName, JSON.stringify(newItems));
      return newItems;
    });
  };

  // Remove an item by id
  const removeItem = (id: string | number) => {
    setItems(prevItems => {
      const newItems = prevItems.filter(item => item.id !== id);
      localStorage.setItem(storeName, JSON.stringify(newItems));
      return newItems;
    });
  };

  // Clear all items from the store
  const clearStore = () => {
    setItems([]);
    localStorage.removeItem(storeName);
  };

  // Get a specific item by id
  const getItem = (id: string | number) => {
    return items.find(item => item.id === id);
  };

  // Effect to load items from localStorage on mount
  useEffect(() => {
    const savedItems = localStorage.getItem(storeName);
    if (savedItems) {
      setItems(JSON.parse(savedItems));
    }
  }, [storeName]);

  return { items, updateItem, addItem, getItem, removeItem, clearStore };
};

export default usePersistentDataStore;
