import React, { createContext, useState, useContext, useEffect } from 'react';
import usePersistentDataStore from '../hooks/usePersistentDataStore';
import { Setting } from '../types/meshdata';
import { getManifest } from '../services/rpc';

interface NamespaceContextProps {
  namespace: string;
  setNamespace: (namespace: string) => void;
  database: string;
  setDatabase: (database: string) => void;
  namespaces: { id: string, name: string }[];
  setNamespaces: React.Dispatch<React.SetStateAction<{ id: string, name: string }[]>>;
  databases: { id: string, name: string }[];
  setDatabases: React.Dispatch<React.SetStateAction<{ id: string, name: string }[]>>;
  entities: { id: string, name: string, search: boolean }[];
  telemetry: { honeycomb_service_name: string, honeycomb_environment: string };
}

const NamespaceContext = createContext<NamespaceContextProps | undefined>(undefined);

export const NamespaceProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { items: Settings, updateItem: upsertSetting, getItem } = usePersistentDataStore<Setting>('Settings');
  const [namespace, setOnlyNamespace] = useState<string>('');
  const [database, setOnlyDatabase] = useState<string>('');
  const [namespaces, setNamespaces] = useState<{ id: string, name: string }[]>([]);
  const [entities, setEntities] = useState<{ id: string, name: string, search: boolean }[]>([]);
  const [databases, setDatabases] = useState<{ id: string, name: string }[]>([]);
  const [telemetry, setTelemetry] = useState<{ honeycomb_service_name: string, honeycomb_environment: string }>({ honeycomb_service_name: '', honeycomb_environment: '' });
  const [manifest, setManifest] = useState<any>({});

  useEffect(() => {
    const savedDatabase = getItem('database');
    if (savedDatabase) {
      setOnlyDatabase(savedDatabase.database);
    }
    const savedNamespace = getItem('namespace');
    if (savedNamespace) {
      setOnlyNamespace(savedNamespace.namespace);
    }
    if (namespace && manifest?.[database]) {
      const supportsFTS = manifest[database].db.search;
      const newEntities = manifest[database].namespaces[namespace]?.entities.map((entity: {name: string, label: string, search: boolean}) => {
        return { id: entity.name, name: entity.label, search: supportsFTS}
      });
      setEntities(newEntities);
    }
  }, [manifest]);

  useEffect(() => {
    const fetchManifest = async () => {
      const response = await getManifest();
      setTelemetry({
        honeycomb_service_name: response.honeycomb_service_name,
        honeycomb_environment: response.honeycomb_environment,
      });
      setManifest(response.manifest);
      const databasesList = Object.entries(response.manifest).map(([key, value]: [string, any]) => ({
        id: key,
        name: value.db.name
      }));
      setDatabases(databasesList);
    };
    fetchManifest();
  }, []);

  useEffect(() => {
    if (database && manifest[database]) {
      const newNamespaces = Object.entries(manifest[database].namespaces).map(([key, value]: [string, any]) => ({
        id: key,
        name: value.label
      }));
      setNamespaces(newNamespaces);
    }
  }, [database, manifest]);

  const setNamespace = (namespace: string) => {
    setOnlyNamespace(namespace);
    upsertSetting({ id: 'namespace', namespace }, true);
    if (namespace && manifest[database]) {
      const supportsFTS = manifest[database].db.search;
      const newEntities = manifest[database].namespaces[namespace].entities.map((entity: {name: string, label: string, search: boolean}) => {
        return { id: entity.name, name: entity.label, search: supportsFTS };
      });
      setEntities(newEntities);
    } else if (entities.length) {
      setEntities([]);
    }
  };

  const setDatabase = (database: string) => {
    setOnlyDatabase(database);
    setOnlyNamespace(''); // Resetting the namespace when database changes
    setNamespaces([]); // Optionally clear namespaces array
    setEntities([]); // Optionally clear entities array
    upsertSetting({ id: 'database', database }, true);
    upsertSetting({ id: 'namespace', namespace: '' }, true); // Persist the reset namespace
  };

  return (
    <NamespaceContext.Provider value={{ namespace, setNamespace, database, setDatabase, databases, setDatabases, namespaces, setNamespaces, entities, telemetry }}>
      {children}
    </NamespaceContext.Provider>
  );
};

export const useNamespace = (): NamespaceContextProps => {
  const context = useContext(NamespaceContext);
  if (!context) {
    throw new Error('useNamespace must be used within a NamespaceProvider');
  }
  return context;
};
