import React from 'react';
import { Box, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { DurableJobExport } from '../../types/meshdata';

interface WorkflowProps {
  workflow: DurableJobExport | undefined;
}

/**
 *  sx={{ '&:nth-of-type(odd)': { backgroundColor: (theme) => theme.palette.action.hover } }}
 */

const DataView: React.FC<WorkflowProps> = ({ workflow }) => {
  // Extract key-value pairs and sort them by key (name) in ascending order
  const rows = workflow?.data ? 
    Object.entries(workflow.data).sort((a, b) => a[0].localeCompare(b[0])) : [];

  const renderTable = (rows: [string, any][], zIndex = 1) => {
    return (
      <TableContainer component={Box} style={{ height: "100%" }}>
        <Table size="small" stickyHeader={true} aria-label="data table">
          <TableHead>
            <TableRow>
              <TableCell component="th" style={{ width: 150, fontWeight: 'bold', zIndex }}>Name</TableCell>
              <TableCell component="th" align="left" style={{ fontWeight: 'bold', zIndex }}>Value</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow key={row[0]}>
                <TableCell scope="row" sx={{ width: '150px', verticalAlign: 'top' }}>
                  {row[0]}
                </TableCell>
                <TableCell align="left">
                  {typeof row[1] === 'object' ?
                    renderTable(Object.entries(row[1] as Record<string, any>), 0) :
                    row[1]
                  }
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    );
  };

  return (
    rows?.length ?
      renderTable(rows) :
      <center style={{ marginTop: '6.63em', fontStyle: 'italic' }}>N / A</center>
  );
};

export default DataView;
