import { useEffect, useRef, useState } from 'react';
import { useParams, useNavigate, useSearchParams } from 'react-router-dom';
import { Grid, List, ListItemIcon, ListItemText, Typography, Box, ListItemButton, useTheme, Select, MenuItem, SelectChangeEvent, IconButton, TextField, CircularProgress, ListSubheader, Chip, TableContainer, Table, TableHead, TableRow, TableCell, TableBody } from '@mui/material';
import ManageHistory from '@mui/icons-material/AccountTree';
import SettingsSuggest from '@mui/icons-material/Handyman';
import NLQueryIcon from '@mui/icons-material/PsychologyAltOutlined';
import FTQueryIcon from '@mui/icons-material/HelpOutlineOutlined';
import SearchIcon from '@mui/icons-material/ManageSearchOutlined';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

import Workflow from './Workflow';
import { truncateWithEllipsis } from '../modules/utils';
import { ask, exportJob, find, findJobs, findWhere } from '../services/rpc';
import { DurableJobExport, SearchResults, StringStringType } from '../types/meshdata';
import { arrayToHash, fromBase64Url, stringToTerminalArray, terminalToString, toBase64Url } from '../modules/utils';
import { useNamespace } from '../context/NamespaceContext';

const Workflows = ({query, prose}: {query?: string, prose?: string}) => {
  const LIMIT_SIZE = 125;
  const BATCH_SIZE = 150;
  const theme = useTheme();
  const { namespace, entities, database } = useNamespace();
  const hasFetchedWorkflows = useRef(false);
  let { workflow_id } = useParams();
  const navigate = useNavigate();
  const [workflows, setWorkflows] = useState<SearchResults>();
  const [isTabular, setIsTabular] = useState(false);
  const [workflow, setWorkflow] = useState<DurableJobExport>();
  const [indexes, setIndexes] = useState<string[]>();
  const [selectedItemId, setSelectedItemId] = useState<string | null>(null);
  const [searchParams, setSearchParams] = useSearchParams();
  const entityQueryParam = searchParams.get('entity') || '*'; // Default to '*' if not specified.
  const queryQueryParam = searchParams.get('query')
  const [ftQuery, setFTQuery] = useState<string>();
  const [nlQuery, setNLQuery] = useState<string>();
  const [prior, setPrior] = useState<string>('');
  const [mode, setMode] = useState<'nl' | 'ft'>(queryQueryParam ? 'ft' : 'nl');
  const [isAsking, setIsAsking] = useState(false);
  const [cursor, setCursor] = useState<string>('0');
  const [reload, setReload] = useState(false); // trigger data reload
  const [cursorStack, setCursorStack] = useState<string[]>(['0']);

  const handleChangeIndex = (event: SelectChangeEvent<string>) => {
    const newIndex = event.target.value as string;
    setSearchParams({ entity: newIndex });
    setCursor('0');
    setCursorStack(['0']);
  };

  useEffect(() => {
    const fetchIndexes = async () => {
      //use entities listed in the dashboard manifest
      const merged: string[] = [];
      entities.forEach((entity: {id: string, name: string, search: boolean}) => {
        //only enable 'findWhere' for entities that support FTS
        if (entity.search) {
          merged.push(entity.id);
        }
        //enable 'findJobs' for all entities
        merged.push(`${entity.id}*`);
      });      
      setIndexes(merged);
    };

    if (namespace !== prior) {  
      setCursor('0');
      setPrior(namespace);
    }
    fetchIndexes();
  }, [entityQueryParam, entities, namespace, database]);

  const fetchWorkflow = async () => {
    if (database && namespace && workflow_id) {
      let baseId = workflow_id.split(':')[3];
      const f1 = baseId.indexOf('-', 1);
      const entity = baseId.substring(0, f1);
      const id = baseId.substring(f1 + 1);
      try {
        const response = await exportJob(entity, id, {}, namespace, database);
        setWorkflow(response);
      } catch (e) {
        console.error(e);
        setWorkflow(undefined);
      }
    }
  };

  useEffect(() => {
    fetchWorkflow();
  }, [workflow_id, entities]);

  useEffect(() => {
    console.log('Assistant Query Change >', query)
    if (prose) {
      handleNLQueryChange(prose);
    }
    if (query) {
      setMode('ft');
      handleFTQueryChange(query, true);
    }
  }, [query, prose]);

  useEffect(() => {
    const query = searchParams.get('query');
    const entity = searchParams.get('entity') || '*';
    const fetchWorkflows = async () => {
      if (query) {
        const ftQuery = fromBase64Url(query);
        if (ftQuery?.trim()?.toUpperCase().startsWith('FT')) {
          handleFTQueryChange(ftQuery, true);
        }
      } else if (entity.includes('*')) {
        const [queryCursor, jobs] = await findJobs({
          match: entity,
          limit: LIMIT_SIZE,
          batch: BATCH_SIZE,
          cursor: cursor || '0',
          namespace,
        },
        {
          namespace,
          entity,
          database,
        });
        setWorkflows({
          count: jobs.length,
          data: jobs.map((jobId: string) => ({ $: jobId })),
          query: entity,
        });
        setIsTabular(false);
        setCursor(queryCursor);
      } else {
        const response = await findWhere(
          entity, 
          { 
            limit: { start: Number(cursor), size: LIMIT_SIZE },
            options: { namespace },
          },
          namespace,
          database,
        );
        setWorkflows(response as SearchResults);
        setIsTabular(true);
        setCursor(((Number(cursor) || 0) + LIMIT_SIZE).toString());
        setCursorStack(['0']);
      }
    };
    if (!hasFetchedWorkflows.current || entityQueryParam || queryQueryParam) {
      if (!namespace) return;
      fetchWorkflows();
      hasFetchedWorkflows.current = true;
    }
  }, [entityQueryParam, queryQueryParam, namespace, reload]);

  useEffect(() => {
    if (workflow_id) {
      setSelectedItemId(workflow_id);
    }
  }, [workflow_id]);

  const handleSelect = (item: { id: string }) => {
    const queryParams = new URLSearchParams(window.location.search);
    navigate(`/workflows/${item.id}?${queryParams.toString()}`, { replace: false });
    setSelectedItemId(item.id);
    setCursorStack(['0']);
  };

  const handleFTQueryChange = async (ftQuery: string, bExec = false) => {
    setFTQuery(ftQuery);
    const params: Record<string, string> = {};
    Array.from(searchParams.entries()).forEach(([key, value]) => {
      params[key] = value;
    });
    params.query = toBase64Url(ftQuery)
    setSearchParams(params);
    if (bExec) {
      try {
        const arr = stringToTerminalArray(ftQuery || '');
        const [cmd, idx, ..._rest] = arr;
        if (cmd?.toUpperCase() === 'FT.AGGREGATE' || cmd?.toUpperCase() === 'FT.SEARCH') {
          if (!idx.startsWith(namespace)) {
            arr[1] = `${namespace}-${idx}`;
          }
        }

        const answer = await find(
          'user',
          { namespace, database },
          ...arr
        );
        if (!(answer?.length)) {
          setWorkflows({
            count: 0,
            data: [],
            query: ftQuery,
          });
          setIsTabular(false);
        } else if (arr[0]?.toUpperCase() === 'FT.AGGREGATE') {
          const count = answer[0];
          setWorkflows({
            count: count as number,
            data: [], //need converter method for the sample data
            query: ftQuery,
          });
          setIsTabular(false);
        } else {
          const count = answer[0];
          const data = arrayToHash(answer as [number, ...Array<string | string[]>]);
          setWorkflows({
            count: count as number,
            data,
            query: ftQuery,
          });
          setIsTabular(true);
        }
      } catch (e) {
        console.error(e);
        setWorkflows({
          count: 0,
          data: [],
          query: ftQuery,
        });
        setIsTabular(false);
      }
    }
  }

  const handleNLQueryChange = async (nlQuery: string, bExec = false) => {
    setNLQuery(nlQuery);
    if (bExec && !isAsking) {
      setIsAsking(true);
      const answer = await ask([{ role: 'user', content: nlQuery as string }], { namespace, database, entity: searchParams.get('entity') || '*'});
      setIsAsking(false);

      setMode('ft');
      const sQuery: string = terminalToString(answer?.ft_query || []);
      handleFTQueryChange(sQuery, true);
    }
  }

  const renderListItem = (item: StringStringType) => {
    let topicOrEngineId = item.$.split(':')[3];
    let f1 = topicOrEngineId.indexOf('-', 1);
    let entity = topicOrEngineId.substring(0, f1);
    const isSelected = selectedItemId === item.$;
    return (
      <ListItemButton
        onClick={() => handleSelect({ id: item.$})}
        selected={isSelected}
        key={`${item.$}`}
        style={{ backgroundColor: isSelected ? theme.palette.action.selected : 'transparent' }}
        alignItems="flex-start"
      >
        <Grid container spacing={2} wrap="nowrap">
          <Grid item>
            
          </Grid>
          <Grid item sm>
            <ListItemText
              primary={
                <Box sx={{display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
                  <Box sx={{p: '0', m: '0', width: '1.5em'}}>
                    <ListItemIcon style={{marginTop: '1em', fontSize: '.65em'}}>
                      {entity.startsWith('-') ? <SettingsSuggest style={{ fontSize: '1.75em' }} /> : <ManageHistory style={{ fontSize: '1.75em' }} />}
                    </ListItemIcon>
                  </Box>
                  <Box>{truncateWithEllipsis(topicOrEngineId, 36)}</Box>
                </Box>
              }
              primaryTypographyProps={{ style: { fontSize: '1em' } }}
            />
          </Grid>
        </Grid>
      </ListItemButton>
    );
  };

  const renderTable = (workflows: StringStringType[] | undefined) => {
    if (!workflows || workflows.length === 0) return null;
  
    // Ensure that 'ID' (representing '$' key) is always the first header
    const additionalHeaders = Object.keys(workflows[0])
      .filter(key => key !== '$');
  
    const headers = ['ID', ...additionalHeaders];
  
    return (
      <TableContainer component={Box} style={{ height: "100%" }}>
        <Table size="small" stickyHeader={true} aria-label="data table">
          <TableHead>
            <TableRow>
              {headers.map(header => (
                <TableCell key={header} component="th" style={{ fontWeight: 'bold' }}>
                  {header.replace(/_./g, (match) => match[1].toUpperCase())}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {workflows.map((item, index) => (
              <TableRow key={index} onClick={() => handleSelect({ id: item.$ })} style={{ cursor: 'pointer', backgroundColor: selectedItemId === item.$ ? 'rgba(0, 0, 0, 0.04)' : 'transparent' }}>
                <TableCell>
                  {truncateWithEllipsis(item.$, 36)}
                </TableCell>
                {additionalHeaders.map(header => (
                  <TableCell key={header}>
                    {item[header]}
                  </TableCell>
                ))}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    );
  };

  // Determine the selected style and border color based on the current mode
  const selectedStyle = {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.secondary.contrastText,
    '&:hover': {
      backgroundColor: theme.palette.primary.main,
    },
    opacity: 1,
    margin: 0,
    width: theme.spacing(6),
    height: theme.spacing(6),
    borderRadius: 0,
    borderWidth: 0,
    borderEndEndRadius: '4px',
    borderStartEndRadius: '4px',
  };

  const borderColor = mode === 'ft' ? theme.palette.primary.main : mode === 'nl' ? theme.palette.primary.main : theme.palette.divider;

  const categorizedIndexes = indexes?.reduce<{ hscan: string[]; ftSearch: string[] }>(
    (acc, index) => {
      index.includes('*') ? acc.hscan.push(index) : acc.ftSearch.push(index);
      return acc;
    },
    { hscan: [], ftSearch: [] }
  ) ?? { hscan: [], ftSearch: [] };

  const isEntity = () => {
    const entity = searchParams.get('entity') || '*';
    return entity.includes('*');
  }

  const paginateBack = () => {
    if (isEntity()) {
      //findJobs: pop the stack and go back 2
      if (cursorStack.length === 1) {
        setCursorStack(['0']);
        setCursor('0');
      } else {
        cursorStack.pop();
        const cursor = cursorStack.pop();
        setCursor(cursor || '0');
      }
    } else {
      //findWhere: go back 2
      setCursor(Math.max(0,((Number(cursor) || 0) - (LIMIT_SIZE * 2))).toString());
    }
    setReload(prev => !prev);
  };

  const paginateNext = () => {
    if (isEntity()) {
      cursorStack.push(cursor);
    }
    //cursor is always set to the next page
    setReload(prev => !prev);
  };

  const handleReload = () => {
    if (isEntity()) {
      //findJobs: pop the stack and go back 1
      const cursor = cursorStack.pop();
      setCursor(cursor || '0');
      if (cursorStack.length === 0) {
        setCursorStack(['0']);
      }
    } else {
      setCursor(Math.max(0,((Number(cursor) || 0) - LIMIT_SIZE)).toString());
    }
    setReload(prev => !prev);
  };

  const renderSearchToolbar = () => {
    return (
      <Box sx={{ padding: theme.spacing(2), display: 'flex', alignItems: 'center', justifyContent: 'space-between', backgroundColor: theme.palette.background.default }}>
        <Typography variant="h5">Workflows
          <Chip
            title="Total count (click to refresh)"
            onClick={resetReload}
            variant="outlined"
            style={{
              cursor: 'pointer',
              paddingTop: '0.25em',
              marginLeft: '1em',
              display: 'inline-block',
              borderRadius: '2px',
              backgroundColor: theme.palette.primary.main,
              color: theme.palette.primary.contrastText,
            }}
            label={isEntity() ?  `Showing ${workflows?.data?.length ?? 0}` : `Total ${workflows?.count || 0}`}
            color="default"
            size="small" />
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Box display="flex" alignItems="center">
            <IconButton sx={{mr: '.5em'}} onClick={paginateBack} title="Page back">
              <ArrowBackIosIcon sx={{fontSize: '.75em'}} />
            </IconButton>
            <Typography
              onClick={handleReload}
              variant="body2"
              title="Reload current page"
              style={{cursor: 'pointer'}}>
              -
            </Typography>
            <IconButton sx={{ml: '.5em', mr: '1em'}} onClick={paginateNext} disabled={cursor === '0'} title="Page next">
              <ArrowForwardIosIcon sx={{fontSize: '.75em'}} />
            </IconButton>
          </Box>
          <Select
            value={entityQueryParam}
            onChange={handleChangeIndex}
            displayEmpty
            inputProps={{ 'aria-label': 'Query All' }}
            style={{ height: '2.5rem' }}
          >
            <ListSubheader>HSCAN Queries</ListSubheader>
            {categorizedIndexes.hscan.map((index) => (
              <MenuItem key={index} value={index}>
                {index}
              </MenuItem>
            ))}
            <MenuItem value="*">[any]*</MenuItem>
            {categorizedIndexes.ftSearch.length > 0 && (
              <ListSubheader>FT.SEARCH Queries</ListSubheader>
            )}
            {categorizedIndexes.ftSearch.map((index) => (
              <MenuItem key={index} value={index}>
                {index}
              </MenuItem>
            ))}
          </Select>
        </Box>
      </Box>
    );
  }

  const renderNLQueryComponent = () => {
    return (
      <Box
        sx={{
          px: '1em',
          height: '5.75em',
          minHeight: '5.75em',
          maxHeight: '5.75em',
          width: '100%',
          display: 'flex',
          flexDirection: 'row',
          position: 'relative',
          overflow: 'hidden',
          borderRadius: '4px',
          opacity: '1',
        }}
      >
        <Box sx={{ 
          flexGrow: 1,
          overflow: 'hidden',
          position: 'relative',
          }}>
          {mode === 'ft' && (
            <TextField
              fullWidth
              disabled={isAsking}
              autoFocus={mode === 'ft'}
              label='FT.SEARCH Query'
              onChange={(e) => handleFTQueryChange(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleFTQueryChange((e.target as HTMLInputElement).value, true);
                }
              }}
              size="small"
              multiline
              value={ftQuery}
              rows={3}
              variant="filled"
              sx={{
                height: '100%',
                position: 'absolute',
                left: 0,
                top: 0,
                border: `2px solid ${borderColor}`,
                borderRadius: '4px',
                borderBottomRightRadius: '0',
              }}
            />
          )}
          {mode === 'nl' && (
            <TextField
              fullWidth
              disabled={isAsking}
              autoFocus={mode === 'nl'}
              label='Natural Language Query'
              onChange={(e) => handleNLQueryChange(e.target.value)}
              onKeyDown={(e: React.KeyboardEvent<HTMLInputElement>) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleNLQueryChange((e.target as HTMLInputElement).value, true);
                }
              }}
              size="small"
              multiline
              value={nlQuery}
              rows={3}
              variant="filled"
              sx={{
                height: '100%',
                position: 'absolute',
                left: 0,
                top: 0,
                border: `2px solid ${borderColor}`,
                borderRadius: '4px',
                borderTopRightRadius: '0',
              }}
            />
          )}
          <IconButton
            title='Execute Query'
            disabled={isAsking}
            sx={{
              position: 'absolute',
              right: '.25em',
              bottom: '.25em',
              p: 0,
              m: 0,
              fontSize: '1.25em'
            }}
            onClick={() => {
              if (mode === 'ft') {
                handleFTQueryChange(ftQuery as string, true);
              } else if (mode === 'nl') {
                handleNLQueryChange(nlQuery as string, true);
              }
            }}
          >
            <SearchIcon sx={{
              fontSize: '1em',
              color: theme.palette.secondary.light
              }} />
          </IconButton>
          <CircularProgress variant="indeterminate" value={90} style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            marginTop: '-12px',
            marginLeft: '-12px',
            display: isAsking ? 'block' : 'none',
          }} />
        </Box>
        <Box sx={{ flexShrink: 0, display: 'flex', flexDirection: 'column' }}>
          <IconButton
            sx={mode === 'nl' ? selectedStyle : {}}
            onClick={() => setMode('nl')}
          >
            <NLQueryIcon sx={{ fontSize: '1.2em' }} />
          </IconButton>
          <IconButton
            sx={mode === 'ft' ? selectedStyle : {}}
            onClick={() => setMode('ft')}
          >
            <FTQueryIcon />
          </IconButton>
        </Box>
      </Box>
    );
  }

  const resetReload = () => {
    setCursor('0');
    setCursorStack(['0']);
    setReload(prev => !prev);
  }

  return (
    <Grid container sx={{ height: '100%' }}>
      <Grid item xs={workflow_id ? 4 : 12} sx={{ borderRight: `1px solid ${theme.palette.divider}`, height: '100%', display: 'flex', flexDirection: 'column' }}>
        {renderSearchToolbar()}
        {renderNLQueryComponent()}
        {isTabular && !workflow_id ? (
          renderTable(workflows?.data)
        ) : (
          <List>
            {workflows?.data?.map((item) => renderListItem(item))}
          </List>
        )}
      </Grid>
      {workflow_id && (
        <Grid item xs={8} sx={{ overflow: 'hidden', height: '100%' }}>
          <Workflow workflow={workflow} workflowId={workflow_id} onRefresh={fetchWorkflow} />
        </Grid>
      )}
    </Grid>
  );  
  
};

export default Workflows;
