import React from 'react';
import { DurableJobExport } from '../../types/meshdata';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Box, Typography } from '@mui/material';
import { useTheme } from '@mui/material/styles';

import { useNamespace } from '../../context/NamespaceContext';

interface WorkflowProps {
  workflow: DurableJobExport | undefined;
}

function getUtcUnixEpoch(decrementHours: number = 0): number {
  const currentTime = Date.now();
  const decrementMilliseconds = decrementHours * 60 * 60 * 1000;
  const adjustedTime = currentTime - decrementMilliseconds;
  return Math.floor(adjustedTime / 1000);
}

const StateView: React.FC<WorkflowProps> = ({ workflow }) => {
  const theme = useTheme();
  const { telemetry } = useNamespace();

  const asString = (value: any) => {
    if (value === null || value === undefined) {
      return '';
    } else if (value && typeof value === 'string' && /^[0-9a-f]{32}$/i.test(value)) {
      return (<a
        href={`https://ui.honeycomb.io/${telemetry.honeycomb_service_name}/environments/${telemetry.honeycomb_environment}/trace?trace_id=${value}&trace_start_ts=${getUtcUnixEpoch(48)}&trace_end_ts=${getUtcUnixEpoch(-12)}`}
        target="_blank"
        title="View trace in Honeycomb"
        rel="noreferrer"
        style={{ color: theme.palette.action.active }}>{value}</a>);
    } else if (typeof value === 'object') {
      return (<pre>{JSON.stringify(value, null, 2)}</pre>);
    }
    return value.toString();
  }


  const renderTableSection = (title: string, data: Record<string, any>) => {
    return (
      <Box>
        <Typography variant="h6" component="div" sx={{ margin: '2em 0 1em 0', fontStyle: 'italic', fontSize: '1em' }}>
          {title}
        </Typography>
        <TableContainer component={Box}>
          <Table stickyHeader aria-label={`${title} table`}>
            <TableBody>
              {Object.entries(data).map(([key, value], index) => (
                <TableRow key={key}>
                  <TableCell scope="row" style={{ width: '150px'}}>
                    {key}
                  </TableCell>
                  <TableCell>{asString(value)}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Box>
    );
  };

  if (!workflow || !workflow.state) {
    return <Typography variant="body1" sx={{ margin: 2 }}>No data available</Typography>;
  }

  return (
    <Box sx={{ width: '100%' }}>
      {workflow.state.data && renderTableSection("Job Data", workflow.state.data)}
      {workflow.state.metadata && renderTableSection("Job Metadata", workflow.state.metadata)}
    </Box>
  );
};

export default StateView;
