import { Buffer } from 'buffer';
import { StringStringType } from '../types/meshdata';

/**
 * Encodes a string to a URL-safe base64 format.
 * @param value - The string to encode.
 * @returns The URL-safe base64 encoded string.
 */
function toBase64Url(value: string): string {
  return Buffer.from(value)
    .toString('base64')
    .replace(/\+/g, '-') // Replace '+' with '-'
    .replace(/\//g, '_') // Replace '/' with '_'
    .replace(/=+$/, ''); // Optional: Remove any trailing '='
}

/**
 * Decodes a URL-safe base64 encoded string.
 * @param value - The base64 string to decode.
 * @returns The decoded string.
 */
function fromBase64Url(value: string): string {
  let base64 = value
    .replace(/-/g, '+') // Replace '-' with '+'
    .replace(/_/g, '/'); // Replace '_' with '/'

  // Optional: Add removed '=' padding back
  const padding = base64.length % 4;
  if (padding === 2) base64 += '==';
  else if (padding === 3) base64 += '=';

  return Buffer.from(base64, 'base64').toString();
}


const terminalToString = (query: string[]): string => {
  if (!query) return '';
  return query.map(item => {
    // Double escape backslashes first to avoid escaping newly added backslashes from wrapping quotes
    let result = item.replace(/\\/g, '\\\\');
    
    // Wrap the string in quotes if it contains spaces or if it originally contained backslashes
    if (result.includes(' ') || item.includes('\\')) {
      result = `"${result}"`;
    }
    
    return result;
  }).join(' ');
};

const stringToTerminalArray = (input: string): string[] => {
  const tokens: string[] = [];
  let match: RegExpExecArray | null;

  // Regular expression to match quoted strings or unquoted tokens
  // Captures: quoted strings including the quotes, unquoted non-space characters
  const regex = /"(?:\\.|[^"\\])*"|\S+/g;

  while ((match = regex.exec(input)) !== null) {
    let token = match[0];
    // Check if the token starts and ends with quotes
    if (token.startsWith('"') && token.endsWith('"')) {
      // Remove the surrounding quotes
      token = token.substring(1, token.length - 1);
      // Unescape any escaped characters (including escaped quotes and double backslashes)
      token = token.replace(/\\(.)/g, '$1');
    }
    tokens.push(token);
  }

  return tokens;
};

function arrayToHash(input: [number, ...Array<string | string[]>]): StringStringType[] {
  const [_count, ...rest] = input;
  const max = rest.length / 2
  const hashes: StringStringType[] = [];
  // Process each item to convert into a hash object
  for (let i = 0; i < max * 2; i += 2) {
    const fields = rest[i + 1] as string[];
    const hash: StringStringType = {};
    for (let j = 0; j < fields.length; j += 2) {
      const fieldKey = fields[j].replace(/^_/, '');
      const fieldValue = fields[j + 1];
      hash[fieldKey] = fieldValue;
    }
    hash['$'] = rest[i] as string;
    hashes.push(hash);
  }
  return hashes;
}

const truncateWithEllipsis = (input: string | undefined, maxLength: number): string => {
  if (!input) {
    return '';
  }
  if (input.length <= maxLength) {
    return input;
  }
  const ellipsis = '...';
  const charsToShow = maxLength - ellipsis.length;
  if (charsToShow <= 0) {
    return ellipsis;
  }
  const frontChars = Math.ceil(charsToShow / 2);
  const backChars = Math.floor(charsToShow / 2);
  return input.slice(0, frontChars) + ellipsis + input.slice(input.length - backChars);
}

const reorder = <T>(
  list: T[],
  startIndex: number,
  endIndex: number
): T[] => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

export { reorder, toBase64Url, fromBase64Url, terminalToString, stringToTerminalArray, arrayToHash, truncateWithEllipsis };
