import React, { useState, useEffect } from 'react';
import { Button, TextField, Typography, useTheme, Box, Checkbox, FormControlLabel } from '@mui/material';
import { QuorumProfile } from '../types/meshdata';
import { truncateWithEllipsis } from '../modules/utils';

import IdeaIcon from '@mui/icons-material/Lightbulb';

interface ConnectionProps {
  connectionType: string;
  throttleValue: number;
  updateThrottle: (throttleUpdate: { guid?: string; topic?: string | null; throttle: number; throttle_all?: boolean }) => Promise<void>;
  selectedItem: QuorumProfile | null;
}

const Connection: React.FC<ConnectionProps> = ({connectionType, throttleValue, updateThrottle, selectedItem }) => {
  const [throttle, setThrottle] = useState<number>(throttleValue);
  const [shouldDisplay, setShouldDisplay] = useState(Boolean(selectedItem));
  const [opacity, setOpacity] = useState(1);
  const [throttleAll, setThrottleAll] = useState<boolean>(false);
  const [throttleSiblingWorkers, setThrottleSiblingWorkers] = useState<boolean>(Boolean(connectionType === 'workers'));
  const theme = useTheme();

  useEffect(() => {
    console.log(selectedItem);
    if (selectedItem) {
      setOpacity(1);
      setShouldDisplay(true);
      setThrottleSiblingWorkers(Boolean(connectionType === 'workers'));
    } else {
      setOpacity(0);
      setTimeout(() => {
        setShouldDisplay(false);
      }, 250); // Assuming the transition duration is 500ms
    }
  }, [selectedItem]);

  useEffect(() => {
    setThrottle(throttleValue);
  }, [throttleValue]);

  const handleSubmit = async (event?: React.MouseEvent) => {
    event?.preventDefault();
    if (connectionType === 'workers' && throttleSiblingWorkers) {
      await updateThrottle({ throttle, topic: selectedItem?.worker_topic });
    } else if (connectionType === 'workers') {
      await updateThrottle({ throttle, topic: selectedItem?.worker_topic, guid: selectedItem?.engine_id });
    } else {
      await updateThrottle({ throttle, guid: selectedItem?.engine_id, topic: null });
    }
  };

  return shouldDisplay ? (
    <Box sx={{
      display: 'flex',
      flexDirection: 'column',
      padding: '1em',
      height: '100%',
      opacity: opacity,
      transition: 'opacity 500ms ease-out, height 500ms ease-out',
      position: 'relative',
    }}>
      <Box sx= {{display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
        {connectionType === 'workers' ? selectedItem?.$type : null}
        <Typography style={{fontSize: '1.25rem'}} variant="h6">{truncateWithEllipsis(connectionType === 'workers' ? selectedItem?.$label : selectedItem?.engine_id, 32)}</Typography>
      </Box>
      {connectionType === 'workers' && <div style={{fontSize: '.65em', marginBottom: '2em'}}>{selectedItem?.engine_id}</div>}
      {connectionType === 'workers' && (
        <FormControlLabel
          control={<Checkbox checked={throttleSiblingWorkers} onChange={(e) => setThrottleSiblingWorkers(e.target.checked)} />}
          label="Throttle Sibling Workers"
        />
      )}
      <Box sx={{display: 'flex', flexDirection:'row', marginTop: '1em'}}>
        <TextField
          autoFocus
          margin="dense"
          sx={{focus: {color: theme.palette.primary.dark}, color: theme.palette.primary.main}}
          id="throttle"
          label="Stream Throttle (ms)"
          type="text"
          variant="outlined"
          onKeyDown={(e) => e.key === 'Enter' && handleSubmit()}
          value={throttle}
          onChange={(e) => setThrottle(isNaN(Number(e.target.value)) ? -1 : Number(e.target.value))}
        />
        <Button
          onClick={handleSubmit}
          color="primary"
          variant="contained"
          style={{
            color: theme.palette.primary.contrastText,
            marginLeft: '1em',
          }}>Update</Button>
      </Box>
      <Typography variant="caption" style={{marginTop: '1em', fontSize: '.75em'}}><IdeaIcon style={{fontSize: '.85em'}}></IdeaIcon> Enter -1 to disable stream processing</Typography>
    </Box>
  ) : null;
};

export default Connection;
