import React, { useState, useEffect } from 'react';
import { Box, Typography, CircularProgress, Link } from '@mui/material';
import { DurableJobExport } from '../../types/meshdata';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';

interface WorkflowProps {
  workflow: DurableJobExport | undefined;
  workflowId: string | undefined;
}

const StatusView: React.FC<WorkflowProps> = ({ workflow }) => {
  const navigate = useNavigate(); // Hook for navigation
  const [duration, setDuration] = useState<string>('');
  const [created, setCreated] = useState<string>('');
  const [updated, setUpdated] = useState<string>('');

  useEffect(() => {
    if (workflow?.state?.metadata?.jc && workflow?.state?.metadata?.ju) {
      const jobCreated = moment(workflow.state.metadata.jc, 'YYYYMMDDHHmmss.SSS');
      const jobUpdated = moment(workflow.state.metadata.ju, 'YYYYMMDDHHmmss.SSS');
      const now = moment();
      const durationMoment = moment.duration(jobUpdated.diff(jobCreated));
      const isJobActive = workflow.status !== 0;
      const durationText = isJobActive ? now.from(jobCreated, true) : durationMoment.humanize();
      setDuration(durationText);
      setCreated(workflow?.state?.metadata?.jc ? moment(workflow.state.metadata.jc, 'YYYYMMDDHHmmss.SSS').format('LLL') : 'N/A');
      setUpdated(isJobActive ? moment().format('LLL') : (workflow.state?.metadata?.ju ? moment(workflow.state.metadata.ju, 'YYYYMMDDHHmmss.SSS').format('LLL') : 'Now'));
    }
  }, [workflow]);

  const renderBackLink = () => {
    const parentKey = workflow?.state?.metadata?.pj || workflow?.state?.metadata?.key;
    if (parentKey) {
      const queryParams = new URLSearchParams(window.location.search);
      const app = workflow?.state?.metadata?.app;
      const jobId = `hmsh:${app}:j:${parentKey}`;    
      return (
        <Typography variant="body1" sx={{ position: 'absolute', top: '10px', left: '10px', fontSize: '1.15em' }}>
          <Link title="View Parent Workflow" onClick={() => navigate(`/workflows/${jobId}?${queryParams.toString()}`, { replace: false })} style={{ cursor: 'pointer' }}>
            ../{parentKey}
          </Link>
        </Typography>
      );
    }
  };

  const renderStatusContent = () => {
    if (workflow?.status === undefined) return <Typography variant="h4">Unknown</Typography>;
    let title: string = 'Active';
    let variant: 'determinate' | 'indeterminate' = 'indeterminate';
    let activitiesRemaining = Number(workflow?.status ?? '0');
    
    if (activitiesRemaining === 0) {
      title = 'Completed';
      variant = 'determinate';
    } else if (activitiesRemaining === -1) {
      title = 'Paused';
      variant = 'determinate';
    } else if (activitiesRemaining < 0) {
      title = 'Interrupted';
      variant = 'determinate';
    }

    return (
      <>
        <Typography variant="h4" style={{marginTop: '1em'}}>{title}</Typography>
        <Typography variant="h6">{`Open/Pending Activities: ${activitiesRemaining}`}</Typography>
        <CircularProgress variant={variant} value={100} />
        <Box sx={{ display: 'flex', padding: '0 1.25em 1em 1.25em', justifyContent: 'space-between', width: '100%', position: 'absolute', bottom: '0' }}>
          <Typography variant="body2"><strong>Start</strong> {`${created}`}</Typography>
          <Typography variant="body2"><strong>Duration</strong> {`${duration}`}</Typography>
          <Typography variant="body2"><strong>Stop</strong> {`${updated}`}</Typography>
        </Box>
      </>
    );
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', height: '100%', gap: 2, position: 'relative' }}>
      {renderStatusContent()}
    </Box>
  );
};

export default StatusView;
