import React, { useState, useMemo, ReactElement } from 'react';
import { BrowserRouter, Routes, Route, useLocation, Link as RouterLink } from 'react-router-dom';
import { createTheme, ThemeProvider, CssBaseline, useMediaQuery, ListItemButton, List, ListItemIcon, ListItemText, Drawer, Box, Select, SelectChangeEvent, MenuItem } from '@mui/material';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Switch from '@mui/material/Switch';
import Container from '@mui/material/Container';
import Tooltip from '@mui/material/Tooltip';
import { useTheme } from '@mui/material/styles';
import MenuIcon from '@mui/icons-material/MenuOutlined';
import HomeIcon from '@mui/icons-material/HomeOutlined';
import NetworkIcon from '@mui/icons-material/HubOutlined';
import ShareIcon from '@mui/icons-material/AccountTreeOutlined';

import brandLogo from './img/hotmesh_144_v.png';
import brandLogoW from './img/hotmesh_144_w.png';

import Connections from './components/Connections';
import { Home } from './components/Home';
import Workflows from './components/Workflows';
import Commander from './components/Commander';

import { useNamespace } from './context/NamespaceContext';

const App: React.FC = () => {
  const [darkMode, setDarkMode] = useState(true);
  const [query, setQuery] = useState('');
  const [prose, setProse] = useState('');
  const [mobileOpen, setMobileOpen] = useState(false);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const { namespace, setNamespace, namespaces, database, setDatabase, databases } = useNamespace();

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  const handleNamespaceChange = (event: SelectChangeEvent<string>) => {
    const newNamespace = event.target.value as string;
    setNamespace(newNamespace);
  };

  const handleDatabaseChange = (event: SelectChangeEvent<string>) => {
    const newDatabase = event.target.value as string;
    setDatabase(newDatabase);
  };

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  const handleThemeChange = () => {
    setDarkMode(!darkMode);
  };

  const startNewChatSession = (message: string) => {
    toggleDrawer();
  };

  const execChatSession = (ftQuery: string, nlQuery: string) => {
    setQuery(ftQuery);
    setProse(nlQuery);
    toggleDrawer();
  };

  const themeConfig = useMemo(() => createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
      primary: {
        main: '#483D8B', // Deep indigo as the primary main color, emphasizing a richer blue tone
        light: '#7a67ee', // A brighter, more saturated light shade for the primary light color
        dark: '#2e2d69', // A very dark indigo for the primary dark color, enhancing contrast
        contrastText: '#ffffff', // High contrast text color, ensuring legibility
      },
      secondary: {
        main: '#584D9B', // A more saturated and vivid blue for the secondary color
        light: '#8e8eff',
        dark: '#2a2ab8',
        contrastText: '#ffffff',
      },
      action: {
        hover: 'rgba(92, 92, 255, 0.25)', // Adjusted to match the more vivid secondary color for hover states
        selected: 'rgba(92, 92, 255, 0.2)', // More pronounced blue background for selected items
        focus: 'rgba(92, 92, 255, 0.12)', // Adjusted to match the more vivid secondary color for focus states
      },
    },
    components: {
      MuiTableCell: {
        styleOverrides: {
          root: {
            padding: '.65em',
          },
        },
      },
      MuiButtonBase: { // Targeting the ButtonBase component for the ripple effect
        styleOverrides: {
          root: {
            // Override the touchRipple color
            '& .MuiTouchRipple-root span': {
              backgroundColor: 'rgba(92, 92, 255, 0.3)', // Adjusted to a vivid blue tint to match the ripple effect
            },
          },
        },
      },
    },
  }), [darkMode]);

  const NavLink = ({ icon, title, to }: { icon: ReactElement, title: string, to: string }) => {
    const location = useLocation();
    const active = location.pathname === to;

    return (
      <Tooltip title={title}>
        <IconButton
          color="inherit"
          component={RouterLink}
          to={to}
          sx={{
            backgroundColor: active ? 'rgba(255, 255, 255, 0.12)' : 'inherit',
            '&:hover': {
              backgroundColor: 'rgba(255, 255, 255, 0.08)',
            },
          }}
        >
          {icon}
        </IconButton>
      </Tooltip>
    );
  };

  const DrawerSet = () => {
    return (
      <Drawer
        anchor="bottom"
        open={drawerOpen}
        onClose={toggleDrawer}
        PaperProps={{
          sx: { height: '450px', borderTopLeftRadius: 8, borderTopRightRadius: 8 }
        }}
      >
        <Commander onExecChat={execChatSession} database={database} namespace={namespace} />
      </Drawer>
    );
  };

  const drawerContent = (
    <div>
      <List>
        {['Home', 'Connections', 'Workflows'].map((text, index) => (
          <ListItemButton key={text} component={RouterLink} to={`/${text.toLowerCase()}`}>
            <ListItemIcon>
              {[<HomeIcon />, <NetworkIcon />, <ShareIcon />][index]}
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItemButton>
        ))}
      </List>
    </div>
  );

  return (
    <ThemeProvider theme={themeConfig}>
      <CssBaseline />
      <div style={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
        <BrowserRouter>
          <AppBar position="static">
            <Toolbar sx={{ px: '1em' }}>
              {isMobile && (
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  edge="start"
                  onClick={handleDrawerToggle}
                >
                  <MenuIcon />
                </IconButton>
              )}
              <img
                title="HotMesh Query Assistant"
                src={darkMode ? brandLogo : brandLogoW}
                alt="Brand Logo"
                style={{ cursor: 'pointer', height: '2.55em', position: 'relative', left: '5px', top: '-7px' }}
                onClick={toggleDrawer}
              />
              <Typography variant="h4" component="div" sx={{ flexGrow: 1, textAlign: 'left' }}>
                HotMesh
              </Typography>
              <Box sx={{ flexGrow: 1, textAlign: 'left' }}>
                <label>
                  App Server:
                  <Select
                    value={database}
                    onChange={handleDatabaseChange}
                    displayEmpty
                    inputProps={{ 'aria-label': 'Select Database' }}
                    style={{
                      height: '2.5rem',
                      color: theme.palette.primary.contrastText,
                      borderColor: theme.palette.primary.contrastText,
                      marginLeft: '1em',
                    }}
                  >
                    <MenuItem value="">- choose db -</MenuItem>
                    {databases.map(({id, name}) => (
                      <MenuItem key={id} value={id}>{name}</MenuItem>
                    ))}
                  </Select>
                </label>
                <label style={{ marginLeft: '2em' }}>
                  Namespace:
                  <Select
                    value={namespace}
                    onChange={handleNamespaceChange}
                    displayEmpty
                    inputProps={{ 'aria-label': 'Select Namespace' }}
                    style={{
                      height: '2.5rem',
                      color: theme.palette.primary.contrastText,
                      borderColor: theme.palette.primary.contrastText,
                      marginLeft: '1em',
                    }}
                  >
                    <MenuItem value="">- choose namespace -</MenuItem>
                    {namespaces.map(({ id, name }) => (
                      <MenuItem key={id} value={id}>[{id}] {name}</MenuItem>
                    ))}
                  </Select>
                </label>
              </Box>
              {!isMobile && (
                <>
                  <NavLink icon={<HomeIcon />} title="Home" to="/" />
                  <NavLink icon={<NetworkIcon />} title="Connections" to="/connections" />
                  <NavLink icon={<ShareIcon />} title="Workflows" to="/workflows" />
                </>
              )}
              <Switch checked={darkMode} onChange={handleThemeChange} />
            </Toolbar>
          </AppBar>
          <Drawer
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
          >
            {drawerContent}
          </Drawer>
          {DrawerSet()}
          <Container style={{ overflowY: 'auto', flexGrow: 1, height: '100vh', padding: 0, maxWidth: '100%' }}>
            <Routes>
              <Route path="/" element={<Home onStartChat={startNewChatSession} />} />
              <Route path="/connections" element={<Connections />} />
              <Route path="/connections/:connection_id" element={<Connections />} />
              <Route path="/workflows" element={<Workflows query={query} prose={prose} />} />
              <Route path="/workflows/:workflow_id" element={<Workflows query={query} prose={prose} />} />
              <Route path="*" element={<Home onStartChat={startNewChatSession} />} />
            </Routes>
          </Container>
        </BrowserRouter>
      </div>
    </ThemeProvider>
  );
};

export default App;
